

import { Directive, Optional, TemplateRef } from '@angular/core';
import { TDSModalRef } from './modal-ref';

@Directive({
  selector: '[tdsModalTitle]',
  exportAs: 'tdsModalTitle'
})
export class TDSModalTitleDirective {
  constructor(@Optional() private modalRef: TDSModalRef, public templateRef: TemplateRef<{}>) {
    if (this.modalRef) {
      this.modalRef.updateConfig({
        title: this.templateRef
      });
    }
  }
}
