

import { Directionality } from '@angular/cdk/bidi';
import { ComponentType, Overlay, OverlayConfig, OverlayRef } from '@angular/cdk/overlay';
import { ComponentPortal, TemplatePortal } from '@angular/cdk/portal';
import { Injectable, Injector, OnDestroy, Optional, SkipSelf, TemplateRef } from '@angular/core';


import { defer, Observable, Subject } from 'rxjs';
import { startWith } from 'rxjs/operators';
import { IndexableObject, isNotNil } from 'tds-ui/core/util';

import { MODAL_MASK_CLASS_NAME } from './modal-config';
import { TDSModalConfirmContainerComponent } from './modal-confirm-container.component';
import { BaseModalContainerComponent } from './modal-container';
import { TDSModalContainerComponent } from './modal-container.component';
import { TDSModalRef } from './modal-ref';
import { ConfirmType, ModalOptions } from './modal-types';
import { applyConfigDefaults, getValueWithConfig, setContentInstanceParams } from './utils';

type ContentType<T> = ComponentType<T> | TemplateRef<T> | string;

@Injectable()
export class TDSModalService implements OnDestroy {
  private openModalsAtThisLevel: TDSModalRef[] = [];
  private readonly afterAllClosedAtThisLevel = new Subject<void>();

  get openModals(): TDSModalRef[] {
    return this.parentModal ? this.parentModal.openModals : this.openModalsAtThisLevel;
  }

  get _afterAllClosed(): Subject<void> {
    const parent = this.parentModal;
    return parent ? parent._afterAllClosed : this.afterAllClosedAtThisLevel;
  }

  readonly afterAllClose: Observable<void> = defer(() =>
    this.openModals.length ? this._afterAllClosed : this._afterAllClosed.pipe(startWith(undefined))
  ) as Observable<void>;

  constructor(
    private overlay: Overlay,
    private injector: Injector,
    @Optional() @SkipSelf() private parentModal: TDSModalService,
    @Optional() private directionality: Directionality
  ) {}

  create<T, R = any>(config: ModalOptions<T, R>): TDSModalRef<T, R> {
    return this.open<T, R>(config.content as ComponentType<T>, config);
  }

  closeAll(): void {
    this.closeModals(this.openModals);
  }

  confirm<T>(options: ModalOptions<T> = {}, confirmType: ConfirmType = 'confirm'): TDSModalRef<T> {
    if ('footer' in options) {
      console.warn(`The Confirm-Modal doesn't support "footer", this property will be ignored.`);
    }
    if (!('width' in options)) {
      options.width = 500;
    }
    if (!('autoClose' in options)) {
      options.autoClose = false;
    }
    options.confirmType = confirmType;
    options.modalType = 'confirm';
    options.className = `tds-modal-confirm tds-modal-confirm-${confirmType} ${options.className || ''}`;
    return this.create(options);
  }

  info<T>(options: ModalOptions<T> = {}): TDSModalRef<T> {
    return this.confirmFactory(options, 'info');
  }

  success<T>(options: ModalOptions<T> = {}): TDSModalRef<T> {
    return this.confirmFactory(options, 'success');
  }

  error<T>(options: ModalOptions<T> = {}): TDSModalRef<T> {
    return this.confirmFactory(options, 'error');
  }

  warning<T>(options: ModalOptions<T> = {}): TDSModalRef<T> {
    return this.confirmFactory(options, 'warning');
  }

  private open<T, R>(componentOrTemplateRef: ContentType<T>, config?: ModalOptions): TDSModalRef<T, R> {
    const configMerged = applyConfigDefaults(config || {}, new ModalOptions());
    const overlayRef = this.createOverlay(configMerged);
    const modalContainer = this.attachModalContainer(overlayRef, configMerged);
    const modalRef = this.attachModalContent<T, R>(componentOrTemplateRef, modalContainer, overlayRef, configMerged);
    modalContainer.modalRef = modalRef;

    this.openModals.push(modalRef);
    modalRef.afterClose.subscribe(() => this.removeOpenModal(modalRef));

    return modalRef;
  }

  private removeOpenModal(modalRef: TDSModalRef): void {
    const index = this.openModals.indexOf(modalRef);
    if (index > -1) {
      this.openModals.splice(index, 1);

      if (!this.openModals.length) {
        this._afterAllClosed.next();
      }
    }
  }

  private closeModals(dialogs: TDSModalRef[]): void {
    let i = dialogs.length;
    while (i--) {
      dialogs[i].close();
      if (!this.openModals.length) {
        this._afterAllClosed.next();
      }
    }
  }

  private createOverlay(config: ModalOptions): OverlayRef {
    const globalConfig: any =  {};
    const overlayConfig = new OverlayConfig({
      hasBackdrop: true,
      scrollStrategy: this.overlay.scrollStrategies.block(),
      positionStrategy: this.overlay.position().global(),
      disposeOnNavigation: getValueWithConfig(config.closeOnNavigation, globalConfig.closeOnNavigation, true),
      direction: getValueWithConfig(config.direction, globalConfig.direction, this.directionality.value)
    });
    if (getValueWithConfig(config.mask, globalConfig.mask, true)) {
      overlayConfig.backdropClass = MODAL_MASK_CLASS_NAME;
    }

    return this.overlay.create(overlayConfig);
  }

  private attachModalContainer(overlayRef: OverlayRef, config: ModalOptions): BaseModalContainerComponent {
    const userInjector = config && config.viewContainerRef && config.viewContainerRef.injector;
    const injector = Injector.create({
      parent: userInjector || this.injector,
      providers: [
        { provide: OverlayRef, useValue: overlayRef },
        { provide: ModalOptions, useValue: config }
      ]
    });

    const ContainerComponent =
      config.modalType === 'confirm'
        ? // If the mode is `confirm`, use `TDSModalConfirmContainerComponent`
          TDSModalConfirmContainerComponent
        : // If the mode is not `confirm`, use `TDSModalContainerComponent`
          TDSModalContainerComponent;

    const containerPortal = new ComponentPortal<BaseModalContainerComponent>(ContainerComponent, config.viewContainerRef, injector);
    const containerRef = overlayRef.attach<BaseModalContainerComponent>(containerPortal);

    return containerRef.instance;
  }

  private attachModalContent<T, R>(
    componentOrTemplateRef: ContentType<T>,
    modalContainer: BaseModalContainerComponent,
    overlayRef: OverlayRef,
    config: ModalOptions<T>
  ): TDSModalRef<T, R> {
    const modalRef = new TDSModalRef<T, R>(overlayRef, config, modalContainer);

    if (componentOrTemplateRef instanceof TemplateRef) {
      modalContainer.attachTemplatePortal(
        new TemplatePortal<T>(componentOrTemplateRef, null!, { $implicit: config.componentParams, modalRef } as any)
      );
    } else if (isNotNil(componentOrTemplateRef) && typeof componentOrTemplateRef !== 'string') {
      const injector = this.createInjector<T, R>(modalRef, config);
      const contentRef = modalContainer.attachComponentPortal<T>(
        new ComponentPortal(componentOrTemplateRef, config.viewContainerRef, injector)
      );
      setContentInstanceParams<T>(contentRef.instance, config.componentParams);
      modalRef.componentInstance = contentRef.instance;
    } else {
      modalContainer.attachStringContent();
    }
    return modalRef;
  }

  private createInjector<T, R>(modalRef: TDSModalRef<T, R>, config: ModalOptions<T>): Injector {
    const userInjector = config && config.viewContainerRef && config.viewContainerRef.injector;

    return Injector.create({
      parent: userInjector || this.injector,
      providers: [{ provide: TDSModalRef, useValue: modalRef }]
    });
  }

  private confirmFactory<T>(options: ModalOptions<T> = {}, confirmType: ConfirmType): TDSModalRef<T> {
    const iconMap: IndexableObject = {
      info: ' tdsi-information-fill',
      success: 'tdsi-success-fill',
      error: 'tdsi-error-fill',
      warning: ' tdsi-warning-fill'
    };
    if (!('iconType' in options)) {
      options.iconType = iconMap[confirmType];
    }
    if (!('cancelText' in options)) {
      // Remove the Cancel button if the user not specify a Cancel button
      options.cancelText = null;
    }
    
    return this.confirm(options, confirmType);
  }

  ngOnDestroy(): void {
    this.closeModals(this.openModalsAtThisLevel);
    this.afterAllClosedAtThisLevel.complete();
  }
}
