

import { ModalOptions } from './modal-types';
import { TDSModalComponent } from './modal.component';

export function applyConfigDefaults(config: ModalOptions, defaultOptions: ModalOptions): ModalOptions {
  return { ...defaultOptions, ...config };
}

export function getValueWithConfig<T>(userValue: T | undefined, configValue: T | undefined, defaultValue: T): T | undefined {
  return typeof userValue === 'undefined' ? (typeof configValue === 'undefined' ? defaultValue : configValue) : userValue;
}

/**
 * Assign the params into the content component instance.
 * @deprecated Should use dependency injection to get the params for user
 * @breaking-change 12.0.0
 */
export function setContentInstanceParams<T>(instance: T, params: Partial<T> | undefined): void {
  Object.assign(instance, params);
}

export function getConfigFromComponent(component: TDSModalComponent): ModalOptions {
  const {
    size,
    centered,
    mask,
    autoClose,
    closable,
    okLoading,
    okDisabled,
    cancelDisabled,
    cancelLoading,
    keyboard,
    noAnimation,
    content,
    componentParams,
    footer,
    zindex,
    width,
    wrapClassName,
    className,
    style,
    title,
    closeIcon,
    maskStyle,
    bodyStyle,
    okText,
    cancelText,
    okType,
    okDanger,
    iconType,
    modalType,
    onOk,
    onCancel,
    onAfterOpen,
    onAfterClose,
    closeOnNavigation,
    autofocus
  } = component;
  return {
    size,
    centered,
    mask,
    autoClose,
    closable,
    okLoading,
    okDisabled,
    cancelDisabled,
    cancelLoading,
    keyboard,
    noAnimation,
    content,
    componentParams,
    footer,
    zindex,
    width,
    wrapClassName,
    className,
    style,
    title,
    closeIcon,
    maskStyle,
    bodyStyle,
    okText,
    cancelText,
    okType,
    okDanger,
    iconType,
    modalType,
    onOk,
    onCancel,
    onAfterOpen,
    onAfterClose,
    closeOnNavigation,
    autofocus
  };
}
