

import { BooleanInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  Output,
  SimpleChanges,
  TemplateRef,
  Type,
  ViewChild,
  ViewContainerRef
} from '@angular/core';

import { Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { InputBoolean } from 'tds-ui/shared/utility';

import { TDSModalContentDirective } from './modal-content.directive';
import { TDSModalFooterDirective } from './modal-footer.directive';
import { TDSModalLegacyAPI } from './modal-legacy-api';
import { TDSModalRef } from './modal-ref';
import { TDSModalTitleDirective } from './modal-title.directive';
import { ModalButtonOptions, ModalOptions, ModalTypes, OnClickCallback, StyleObjectLike, TDSModalSize } from './modal-types';
import { TDSModalService } from './modal.service';
import { getConfigFromComponent } from './utils';
@Component({
  selector: 'tds-modal',
  exportAs: 'tdsModal',
  template: `
    <ng-template>
      <ng-content></ng-content>
    </ng-template>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TDSModalComponent<T = any, R = any> implements OnChanges, TDSModalLegacyAPI<T, R>, OnDestroy {
  static ngAcceptInputType_mask: BooleanInput;
  static ngAcceptInputType_maskClosable: BooleanInput;
  static ngAcceptInputType_closeOnNavigation: BooleanInput;
  static ngAcceptInputType_visible: BooleanInput;
  static ngAcceptInputType_closable: BooleanInput;
  static ngAcceptInputType_okLoading: BooleanInput;
  static ngAcceptInputType_okDisabled: BooleanInput;
  static ngAcceptInputType_cancelDisabled: BooleanInput;
  static ngAcceptInputType_cancelLoading: BooleanInput;
  static ngAcceptInputType_keyboard: BooleanInput;
  static ngAcceptInputType_noAnimation: BooleanInput;
  static ngAcceptInputType_okDanger: BooleanInput;
  static ngAcceptInputType_centered: BooleanInput;

  @Input() @InputBoolean() mask?: boolean;
  @Input() @InputBoolean() autoClose?: boolean;
  @Input() @InputBoolean() closeOnNavigation?: boolean;
  @Input() @InputBoolean() visible: boolean = false;
  @Input() @InputBoolean() closable: boolean = true;
  @Input() @InputBoolean() okLoading: boolean = false;
  @Input() @InputBoolean() okDisabled: boolean = false;
  @Input() @InputBoolean() cancelDisabled: boolean = false;
  @Input() @InputBoolean() cancelLoading: boolean = false;
  @Input() @InputBoolean() keyboard: boolean = true;
  @Input() @InputBoolean() noAnimation = false;
  @Input() @InputBoolean() centered = false;
  @Input() size:TDSModalSize = "md";
  @Input() content?: string | TemplateRef<{}> | Type<T>;
  @Input() componentParams?: T;
  @Input() footer?: string | TemplateRef<{}> | Array<ModalButtonOptions<T>> | null;
  @Input() zindex: number = 60;
  @Input() wrapClassName?: string;
  @Input() className?: string;
  @Input() style?: object;
  @Input() title?: string | TemplateRef<{}>;
  @Input() closeIcon: string | TemplateRef<void> = 'close';
  @Input() maskStyle?: StyleObjectLike;
  @Input() bodyStyle?: StyleObjectLike;
  @Input() okText?: string | null;
  @Input() cancelText?: string | null;
  @Input() okType: "default" | "primary" | "dashed" | "danger" | "link" | "text" | null = 'primary';
  @Input() @InputBoolean() okDanger: boolean = false;
  @Input() iconType: string = 'question-circle'; // Confirm Modal ONLY
  @Input() modalType: ModalTypes = 'default';
  @Input() autofocus: 'ok' | 'cancel' | 'auto' | null = 'auto';

  // TODO(@hsuanxyz) Input will not be supported
  @Input()
  @Output()
  readonly onOk: EventEmitter<T> | OnClickCallback<T> | any = new EventEmitter<T>();

  // TODO(@hsuanxyz) Input will not be supported
  @Input()
  @Output()
  readonly onCancel: EventEmitter<T> | OnClickCallback<T> | any = new EventEmitter<T>();

  @Output() readonly onAfterOpen = new EventEmitter<void>();
  @Output() readonly onAfterClose = new EventEmitter<R>();
  @Output() readonly visibleChange = new EventEmitter<boolean>();

  @ViewChild(TemplateRef, { static: true }) contentTemplateRef!: TemplateRef<{}>;

  @ContentChild(TDSModalTitleDirective, { static: true, read: TemplateRef })
  set modalTitle(value: TemplateRef<any>) {
    if (value) {
      this.setTitleWithTemplate(value);
    }
  }

  @ContentChild(TDSModalContentDirective, { static: true, read: TemplateRef })
  contentFromContentChild!: TemplateRef<any>;

  @ContentChild(TDSModalFooterDirective, { static: true, read: TemplateRef })
  set modalFooter(value: TemplateRef<any>) {
    if (value) {
      this.setFooterWithTemplate(value);
    }
  }
  get width(): number {
    let result = 500;
    switch (this.size) {
      case "sm":
        result = 300;
        break;
      case "md":
        result = 500;
        break;
      case "lg":
        result = 800;
        break;
      case "xl":
        result = 1140;
        break;
      default:
        result = 500;
        break;
    }
    return result;
  }
  private modalRef: TDSModalRef | null = null;
  private destroy$ = new Subject<void>();

  get afterOpen(): Observable<void> {
    // Observable alias for afterOpen
    return this.onAfterOpen.asObservable();
  }

  get afterClose(): Observable<R> {
    // Observable alias for afterClose
    return this.onAfterClose.asObservable();
  }

  constructor(private cdr: ChangeDetectorRef,
    private modal: TDSModalService,
    private viewContainerRef: ViewContainerRef) { }

  open(): void {
    if (!this.visible) {
      this.visible = true;
      this.visibleChange.emit(true);
    }

    if (!this.modalRef) {

      const config = this.getConfig();
      this.modalRef = this.modal.create(config);

      // When the modal is implicitly closed (e.g. closeAll) the visible needs to be set to the correct value and emit.
      this.modalRef.afterClose
        .asObservable()
        .pipe(takeUntil(this.destroy$))
        .subscribe(() => {
          this.close();
        });
    }
  }

  close(result?: R): void {
    if (this.visible) {
      this.visible = false;
      this.visibleChange.emit(false);
    }

    if (this.modalRef) {
      this.modalRef.close(result);
      this.modalRef = null;
    }
  }

  destroy(result?: R): void {
    this.close(result);
  }

  triggerOk(): void {
    this.modalRef?.triggerOk();
  }

  triggerCancel(): void {
    this.modalRef?.triggerCancel();
  }

  getContentComponent(): T | void {
    return this.modalRef?.getContentComponent();
  }

  getElement(): HTMLElement | void {
    return this.modalRef?.getElement();
  }

  getModalRef(): TDSModalRef | null {
    return this.modalRef;
  }

  private setTitleWithTemplate(templateRef: TemplateRef<{}>): void {
    this.title = templateRef;
    if (this.modalRef) {
      // If modalRef already created, set the title in next tick
      Promise.resolve().then(() => {
        this.modalRef!.updateConfig({
          title: this.title
        });
      });
    }
  }

  private setFooterWithTemplate(templateRef: TemplateRef<{}>): void {
    this.footer = templateRef;
    if (this.modalRef) {
      // If modalRef already created, set the footer in next tick
      Promise.resolve().then(() => {
        this.modalRef!.updateConfig({
          footer: this.footer
        });
      });
    }

    this.cdr.markForCheck();
  }

  private getConfig(): ModalOptions {
    const componentConfig = getConfigFromComponent(this);
    componentConfig.viewContainerRef = this.viewContainerRef;
    componentConfig.content = this.content || this.contentFromContentChild;
    return componentConfig;
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { visible, ...otherChanges } = changes;

    if (Object.keys(otherChanges).length && this.modalRef) {
      this.modalRef.updateConfig(getConfigFromComponent(this));
    }

    if (visible) {
      if (this.visible) {
        this.open();
      } else {
        this.close();
      }
    }
  }

  ngOnDestroy(): void {
    this.modalRef?._finishDialogClose();
    this.destroy$.next();
    this.destroy$.complete();
  }
}
