


import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnDestroy, Output } from '@angular/core';
import { Subject } from 'rxjs';
import { isPromise } from 'tds-ui/core/util';

import { TDSModalRef } from './modal-ref';
import { ModalButtonOptions, ModalOptions } from './modal-types';

@Component({
  selector: 'div[tds-modal-footer]',
  exportAs: 'tdsModalFooterBuiltin',
  template: `
    <ng-container *ngIf="config.footer; else defaultFooterButtons">
      <ng-container *tdsStringTemplateOutlet="config.footer; context: { $implicit: config.componentParams, modalRef: modalRef }">
        <div *ngIf="!buttonsFooter" [innerHTML]="config.footer"></div>
        <ng-container *ngIf="buttonsFooter">
        <div class="w-full flex justify-end p-4">
        <button
        class="ml-2 min-w-[100px]"
            *ngFor="let button of buttons"
            tds-button
            (click)="onButtonClick(button)"
            [hidden]="!getButtonCallableProp(button, 'show')"
            [loading]="getButtonCallableProp(button, 'loading')"
            [disabled]="getButtonCallableProp(button, 'disabled')"          
            [size]="button.size!"
          >
            {{ button.label }}
          </button>
        </div>
          
        </ng-container>
      </ng-container>
    </ng-container>
    <ng-template #defaultFooterButtons>
     <div class="w-full flex justify-end p-4">
     <button
     class="ml-2"
     *ngIf="config.cancelText !== null"
     [attr.cdkFocusInitial]="config.autofocus === 'cancel' || null"
     tds-button
     class="min-w-[100px]"
     color="secondary"
     (click)="onCancel()"      
     [disabled]="config.cancelDisabled!"
   >
     {{ config.cancelText || locale.cancelText }}
   </button>
   <button
     *ngIf="config.okText !== null"
     [attr.cdkFocusInitial]="config.autofocus === 'ok' || null"
     tds-button   
     (click)="onOk()"        
     [disabled]="config.okDisabled!"
     class="ml-2 min-w-[100px]"
   >
     {{ config.okText || locale.okText }} 
       </button>
     </div>
    </ng-template>
  `,
  host: {
    class: 'tds-modal-footer'
  },
  changeDetection: ChangeDetectionStrategy.Default
})
export class TDSModalFooterComponent implements OnDestroy {
  buttonsFooter = false;
  buttons: ModalButtonOptions[] = [];
  @Output() readonly cancelTriggered = new EventEmitter<void>();
  @Output() readonly okTriggered = new EventEmitter<void>();
  @Input() modalRef!: TDSModalRef;
  private destroy$ = new Subject<void>();
  locale = {
    okText: "Lưu",
    cancelText: "Hủy",
  }
  constructor(public config: ModalOptions) {

    if (Array.isArray(config.footer)) {
      this.buttonsFooter = true;
      this.buttons = (config.footer as ModalButtonOptions[]).map(mergeDefaultOption);
    }

  }

  onCancel(): void {
    this.cancelTriggered.emit();
  }

  onOk(): void {
    this.okTriggered.emit();
  }

  /**
   * Returns the value of the specified key.
   * If it is a function, run and return the return value of the function.
   */
  getButtonCallableProp(options: ModalButtonOptions, prop: keyof ModalButtonOptions): boolean {
    const value = options[prop];
    const componentInstance = this.modalRef.getContentComponent();
    return typeof value === 'function' ? value.apply(options, componentInstance && [componentInstance]) : value;
  }

  /**
   * Run function based on the type and set its `loading` prop if needed.
   */
  onButtonClick(options: ModalButtonOptions): void {
    const loading = this.getButtonCallableProp(options, 'loading');
    if (!loading) {
      const result = this.getButtonCallableProp(options, 'onClick');
      if (options.autoLoading && isPromise(result)) {
        options.loading = true;
        result.then(() => (options.loading = false)).catch(() => (options.loading = false));
      }
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}

function mergeDefaultOption(options: ModalButtonOptions): ModalButtonOptions {
  return {
    type: null,
    size: 'md',
    autoLoading: true,
    show: true,
    loading: false,
    disabled: false,
    color:'secondary',
    ...options
  };
}
